<template>
    <div id="wrapper">
        <v-head></v-head>
        <div class="selectBoard">
            <div class="firstBar">
            学院 <el-select v-model="academySelected" class="selectBar" clearable placeholder="请选择学院">
                        <el-option label="机械与汽车工程学院" value="机械与汽车工程学院"></el-option>
                         <el-option label="建筑学院" value="建筑学院"></el-option>
                         <el-option label="土木与交通学院" value="土木与交通学院"></el-option>
                         <el-option label="电子与信息学院" value="电子与信息学院"></el-option>
                         <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
                         <el-option label="化学与化工学院" value="化学与化工学院"></el-option>
                         <el-option label="轻工科学与工程学院" value="轻工科学与工程学院"></el-option>
                         <el-option label="食品科学与工程学院" value="食品科学与工程学院"></el-option>
                         <el-option label="数学学院" value="数学学院"></el-option>
                         <el-option label="物理与光电学院" value="物理与光电学院"></el-option>
                         <el-option label="经济与贸易学院" value="经济与贸易学院"></el-option>
                         <el-option label="建筑自动化科学与工程学院" value="建筑自动化科学与工程学院"></el-option>
                         <el-option label="计算机科学与工程学院" value="计算机科学与工程学院"></el-option>
                         <el-option label="电力学院" value="电力学院"></el-option>
                         <el-option label="生物科学与工程学院" value="生物科学与工程学院"></el-option>
                         <el-option label="环境与能源学院" value="环境与能源学院"></el-option>
                         <el-option label="软件学院" value="软件学院"></el-option>
                         <el-option label="工商管理学院" value="工商管理学院"></el-option>
                         <el-option label="公共管理学院" value="公共管理学院"></el-option>
                         <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
                         <el-option label="外国语学院" value="外国语学院"></el-option>
                         <el-option label="法学院" value="法学院"></el-option>
                         <el-option label="新闻与传播学院" value="新闻与传播学院"></el-option>
                         <el-option label="艺术学院" value="艺术学院"></el-option>
                         <el-option label="体育学院" value="体育学院"></el-option>
                         <el-option label="设计学院" value="设计学院"></el-option>
                         <el-option label="医学院" value="医学院"></el-option>
                         <el-option label="国际教育学院" value="国际教育学院"></el-option>
                  
                </el-select>
            学年 <el-select v-model="gradeSelected" class="selectBar" clearable placeholder="请选择学年">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            选择学期 <el-select v-model="semesterSelected" class="selectBar" clearable placeholder="请选择学期">
                    <el-option label="第一学期" value=1></el-option>
                    <el-option label="第二学期" value=2></el-option>
                  
                  </el-select>
            
            <el-button type="primary" @click="searchCourse" id="query">查询</el-button>
            </div>
            <div class="clear"></div>
          
        </div>
              <div class="tableBar">
                 <div id="table">
                    <el-table
                :data="tableData"
                id="teaCourseTable"
                border
                :default-sort = "{prop: 'name',order: 'descending'}"
                >
                <el-table-column  
                  prop="name"
                  label="课程名称"
                  min-width="10%"
                  sortable>
                </el-table-column>
                <el-table-column
                  prop="Id"
                  label="课程ID"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="academy"
                  label="开课学院"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="teacher"
                  label="开课教师"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="grade"
                  label="学年"
                min-width="20%"
                sortable>
                </el-table-column>
                <el-table-column
                  prop="semester"
                  label="学期"
                min-width="20%"
                sortable>
                </el-table-column>
                
                <el-table-column
                  prop="operation"
                  label="操作"
                min-width="20%">
                <template slot-scope="scope">
                <el-button @click="viewCourse(scope.row)" type="text" size="medium">课程信息</el-button>    
                <el-button @click="modifyCourse(scope.row)" type="text" size="medium">上课学生</el-button>
                <br>
				 <el-button @click="publishCourse(scope.row)" type="text" size="medium">{{scope.row.publish}}</el-button>
                 <el-button @click="deleteCourse(scope.row)" type="text" size="medium">删除</el-button>
                </template>
                </el-table-column>
              </el-table>
               <el-button @click="addCourse()" type="primary" size="medium">录入</el-button>
                </div>
             </div>
    </div>
</template>
<script>
import vHead from '../commons/AdminstratorNav.vue';
export default {
    created(){
        if(localStorage.getItem("first")==="1"){
            console.log(localStorage)
        this.academySelected=localStorage.getItem("depSelected");
        this.gradeSelected=localStorage.getItem("yearSelected");
        this.semesterSelected=localStorage.getItem("termsSelected");
        console.log(this.academySelected);
        console.log(this.gradeSelected);
        console.log(this.semesterSelected);

        var vm=this;
        this.tableData=[]
        $.ajax({
            type:'post',
            dataType:'json',
            url:'/administor/getCourses',
            data:{
                "department":this.academySelected,
                "years":this.gradeSelected,
                "terms":this.semesterSelected
            },
            success:function(response){
                if(response.string!=""){
                    vm.analysisCourse(vm,response.string);
                }else{
                    
                }
            },
            error:function(err){
                console.log(err);
                console.log("error");
            
            }
        })
            localStorage.setItem("first","0")
        }
    },
  components:{
      vHead,
  },
  data(){
      return{
	      
          academySelected:'',
          gradeSelected:'',
          semesterSelected:'',
          
		   options:[{
		  //value:'88',
		  //label:'555'
		  }],
		  
		  
          tableData:[]
      }
  },
  methods:{
    searchCourse(){
        var vm=this;
        localStorage.setItem("depSelected",this.academySelected);
        localStorage.setItem("yearSelected",this.gradeSelected);
        localStorage.setItem("termsSelected",this.semesterSelected);

        this.tableData=[]
        $.ajax({
            type:'post',
            dataType:'json',
            url:'/administor/getCourses',
            data:{
                "department":this.academySelected,
                "years":this.gradeSelected,
                "terms":this.semesterSelected
            },
            success:function(response){
                if(response.string!=""){
                    vm.analysisCourse(vm,response.string);
                }else{
                    vm.$message({
                        type:'info',
                        message:'未加载到课程'
                    })
                }
            },
            error:function(err){
                console.log(err);
                console.log("error");
                vm.$message({
                    type:'warning',
                    message:'网络故障'
                })
            }
        })

    },
     analysisCourse(vm,classList){
        vm.options2=[]
        var clazz=classList.split(';');
        console.log(clazz.length);
        for(var i=0;i<clazz.length;i++){
            var dist=new Array();
            var ele=clazz[i].split(',');
            for(var j=0;j<ele.length;j++){
                var eles=ele[j].split(':');
                console.log(eles[0],'>',eles[1]);
                dist[eles[0]]=eles[1]
            }
             vm.tableData.push(dist);
        }
        console.log(vm.tableData)
     },
    loadGrade(){
	var myDate=new Date();
	var k=myDate.getFullYear();
	k=k-10;
	while(k!=2018){
	var l=k+1;
	var n=k+'-'+l;
	var m={value:n,label:n,}
	this.options.push(m);
	k=k+1;
	//console.log(k);
	}
	},
    viewCourse(row){
          this.$router.push('/adminstrator/xuanCourseManagementInfo')
    },
    modifyCourse(row){
          this.$router.push('/adminstrator/xuanCourseManagement')
    },
	publishCourse(row){
	 
	 if(row.pub==0){
     row.pub=1
	 row.publish='暂停'
	 alert("发布成功")
	 }
	 else{
	 row.pub=0
	 row.publish='发布'
	 alert("已暂停")
	 }
	 
	
    },
    deleteCourse(row){

    },
    addCourse()
    {
          this.$router.push({path: '/adminstrator/xuanCourseAdd'})
    }

  },
   mounted(){
	this.loadGrade();
	}
}
</script>
<style scoped>
#info{
    width:96%;
    margin:0px auto;
    padding-top:10px;
}
#infoTable{
    border-color:#eefff6;
   
}
#query{

    width:100px;
}
.selectBoard{
    margin-top: 10px;
    background-color: #eef1f6;
    width:100%;
    height:50px;
}
.firstBar{
   
    margin-left:5px;
}
.firstBar .selectBar{
    margin:5px 20px 5px 0px;
    
}
.scoreBoard{
    margin-top: 10px;
    background-color: #eef1f6;
    width:100%;
    height:800px;
}
.lbl{
    font-family: "微软雅黑";
    color:#CDC1C5;
}
#table{
     margin:0px auto;
    margin-top:20px;
    width:95%;
}
</style>




